@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row-content {
  margin:0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height:400px;
}

*, *:before, *:after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background: #f9f9f9;
  font-size: 16px;
  font-family: 'Raleway', sans-serif;
}
.modal-text{
  font-family: 'Raleway', sans-serif;
}

.navbar-dark {
  background-color: rgba(0, 0, 0, 0.801)!important;
  font-family: 'Raleway', sans-serif;
}

@-webkit-keyframes enter{
  0% {opacity: 0%;}
  100% {opacity: 100%;}
}

@keyframes enter{
  0% {opacity: 0%;}
  100% {opacity: 100%;}
}

.mature{
  color:red;
  right: 10px;
  position: absolute;
}

.navbar-cust{
  max-height: 40px;
}

.navbar-text{
  color: rgba(247, 247, 247, 0.856);
  margin-top: -10px;
}

.container1{
  padding: 0.1em;
  float: left;
  width: 100%;
}
@media screen and (max-width: 640px){
  .container{
    display: block;
    width: 100%;
  }
}

@media screen and (min-width: 900px){
  .container1{
    width: 100%;
  }
}

.container1 .title{
  color: #1a1a1a;
  text-align: center;
  margin-bottom: 1px;
}

.content {
  position: relative;
  width: 100%;
  max-width: 500px;
  margin: auto;
  overflow: hidden;
}
@media screen and (min-width: 900px){
.content .content-overlay{
  background: rgb(0, 0, 0);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 1;
  transition: all 0.4s ease-in-out 0s;
}
}

.button-like{
  position: absolute;
  color: white;
  font-size: 50px!important;
  bottom:15px;
  left:15px;
  opacity:1;
}

.modal-rating{
  position:absolute;
  bottom:10px;
}

.content-details:hover{
  background-color:black;
}

.content-image{
  width: 100%;
  -webkit-animation: enter 1s ease-in;
          animation: enter 1s ease-in;
}

.content-details {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  padding-top: 2em;
  width: 100%;
  height:100%;
  top: 60%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  transition: all 0.3s ease-in-out 0s;
  font-family: 'Raleway', sans-serif;
}
@media screen and (min-width: 1300px){
.content:hover .content-details{
  top: 50%;
  left: 50%;
  opacity: 0.8;
}
}
.content-details:hover .content-image{
  opacity:0.8 ;
}

.content-details h3{
  color: rgb(255, 255, 255);
  font-weight: 500;
  letter-spacing: 0.15em;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

.content-details p{
  color: rgb(255, 255, 255);
  font-size: 0.8em;
}

.fadeIn-bottom{
  top: 80%;
}

.fadeIn-top{
  top: 20%;
}

.fadeIn-left{
  left: 20%;
}

.fadeIn-right{
  left: 80%;
}
